<template>
  <div class="verify">
     <van-popup v-model="login" position="bottom">
       <van-picker
        show-toolbar
        title=""
        :columns="selectList" 
        @cancel="onCancel"
        @confirm="onConfirm"
       />
     </van-popup>
     <van-dialog
        v-model="show"
        title="确认角色信息"
        confirmButtonText='参与活动'
        cancelButtonText='换一个'
        item-height='100'
        :beforeClose="beforeClose"
        show-cancel-button
      >
        <div class="windowInfo">
          <img :src="selectPic"> 
          <span>{{nickName}}</span>
        </div>
      </van-dialog>
      <van-loading size="24px">加载中...</van-loading>
  </div>
</template>

<script>
import Vue from 'vue';  
import axios from 'axios'
import { Dialog , Picker, Popup, Loading  } from 'vant';

Vue.use(Picker);
Vue.use(Popup);
Vue.use(Loading);
export default {
  components: {
     [Dialog.Component.name]: Dialog.Component
  },
  data () {
    return {
      show: false,//确认信息弹窗
      login: false,//下拉选项
      selectList: [],
      selectObj: {},//选择的obj
      selectPic:'',
      nickName: '',
      mobileSelectObj: null,
    }
  },
  methods: {
    changeNext() {
      this.login = false;
    },
    // 下拉选项框确定按钮
    onConfirm(value, index) {
      this.login = false;
      this.show = true;
      this.selectObj = value
      this.selectPic = value.icon
      this.nickName = value.nick
    },
     // 下拉选项框取消按钮
    onCancel() {
      this.login = false;
      this.show = true;
    },
    beforeClose(action, done) {
      let _this = this;
      if(action === 'confirm') {
        console.log('确定按钮')
        let url = `/api/mhshow_h5/init_roleinfo`
        let formData = new FormData();
        let icon = this.selectObj.icon;
        let iconNum = icon.slice(icon.lastIndexOf("/")+1,icon.lastIndexOf("."))
        formData.append("role_id", this.selectObj.roleld);
        formData.append("role_name", this.selectObj.nick);
        formData.append("icon", iconNum);
        formData.append("hostname", this.selectObj.serverName);
        formData.append("host", this.selectObj.server);
        formData.append("from", this.selectObj.appKey);
        alert('formData' + JSON.stringify(formData))
        alert('this.selectObj' + JSON.stringify(this.selectObj))
        axios.post(url, formData).then((res) => {
          alert('res.coderes.coderes.coderes.code' + JSON.stringify(res))
          if(res && res.data.code === 200) {
            localStorage.setItem('role_id', _this.selectObj.roleld);
            _this.$router.push({path:'/index'});
          }
        }).catch(err => {
          alert('请求错误')
        })
        done()
      } else if(action === 'cancel') {
        this.login = true
        done()
      }
    },
    getParameter: function (name) {
      var query = window.location.search;
      var iLen = name.length;
      var iStart = query.indexOf(name);
      if (iStart == -1)
          return "";
      iStart += iLen + 1;
      var iEnd = query.indexOf("&", iStart);
      if (iEnd == -1)
          return query.substring(iStart);
      return query.substring(iStart, iEnd);
    },
    isWeiXin: function() {
      var ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/MicroMessenger/i) == 'micromessenger') {
          return true; // 是微信端
      } else {
          return false;
      }
    },
  },
  mounted () {
    let meta = document.getElementsByTagName('meta');
    meta[1].content = "initial-scale=1.0"
    let _this = this;
    //关键参数
    let role_id = this.getParameter('role_id');

    //微信端验证
    if (this.isWeiXin()) {
      
      // let url = 'https://exth5.dev.16163.com?role_id='
      let redirect_uri = 'https%3a%2f%2fexth5.dev.16163.com%3frole_id%3d';
      let code = this.getParameter('code');
      let appid = 'wx0376800bd1071c94';

      //微信回调含code调用免登陆接口，否则跳转微信验证获取code
      if (code) {
          localStorage.setItem('appToken',code)
          localStorage.setItem('role_id',role_id)
          this.$router.push({ path: '/index' })
          
      } else {
        window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${redirect_uri}${role_id}&response_type=code&scope=snsapi_userinfo&state=mhshow_h5`;
      }
    } else {
      //大神端验证
      ds.ready().then(function() {
        if (ds.isGodlike) {
          ds.checkLogined().then((res) => {
            return res
          }).then(res => {
            if(res && res.result.isLogined) {
              ds.getAccessToken({
                appId: '5d81e28d1da86cffea4b7829'
              }).then((res) => {
                //储存为appToken
                return res
              })
              ds.getBindGameRoleList({
                 gameCode: '',
                 minLevel: 0
               }).then((res) => {
                 if(res && res.result && res.result.list.length > 0) {
                  let arr = [];
                  for(let i = 0; i < res.result.list.length; i++) {
                    res.result.list[i].text = `${res.result.list[i].nick} - ${res.result.list[i].serverName}`
                  }
                  _this.selectList = res.result.list;
                  _this.selectObj = res.result.list[0];
                  _this.nickName = _this.selectList[0].nick;
                  _this.selectPic = _this.selectList[0].icon;
                  _this.show = true;
                 } else {
                    Dialog.alert({
                      message: '绑定梦幻手游角色后即可参与活动！',
                      confirmButtonText: '去绑定'
                    }).then(() => {
                      ds.openGameRoleBindingPage({
                        gameCode: 'g18'
                      });
                    });
                 }
               })    
            }else if (res && !res.result.isLogined) {
              Dialog.alert({
                message: '登录且绑定梦幻手游角色后即可参与活动！',
                confirmButtonText: '去登录'
              }).then(() => {
                // on close
                ds.openLoginPage();
              });
            }
          })
          //外部浏览器验证
        } else {
          _this.$router.push({ path: '/index' })
        }
      });
    }



  },
}
</script>


<style lang='less' scoped>
  .swal-footer {
    text-align: center
  }
  .verify {
    font-size: 16px;
    width: 100%;
    .windowInfo {
      color: red;
      margin-top: .01rem;
      img {
        border-radius: 50px;
        vertical-align: middle;
        width: 13%
      }
      span{
        margin-left: 7px
      }
    }
  }
  
</style>



